import { AsyncAppAction } from '..';
import { business, businessOwner } from '../../types/business';
import { loading } from '../../utils/general';
import http from '../../utils/http';
import { updateSelectedBusiness, updateBusinessList, updateOwnersList } from '../actionCreators/business';

/**
 * list all business
 * @returns {Array<business>}
 */
export const listAllbusiness = (
  limit = 50,
  offset = 0
): AsyncAppAction<{data: business[], total: number}> => (dispatch) => new Promise((resolve, reject) => {
  loading(true);
  const url = `v1/business?include=accounts&limit=${limit}&offset=${offset}`;

  http
    .get<{data: business[], total: number}>(url)
    .then(({ data }) => {
      dispatch(updateBusinessList(data.data));
      resolve(data);
    })
    .catch((e) => {
      if (e.response && e.response.data) {
        reject(e.response.data);
      } else {
        reject(e);
      }
    })
    .finally(() => {
      loading(false);
    });
});

/**
 * create business
 * @returns {Array<business>}
 */
export const createbusiness = (payload: {
  legalName: string
  entityType: string
  dba?: string
  email: string
  idType: string
  idNumber: string
}): AsyncAppAction<business> => (dispatch) => new Promise((resolve, reject) => {
  loading(true);
  http
    .post<business>('v1/business', payload)
    .then(({ data }) => {
      dispatch(updateSelectedBusiness(data));
      resolve(data);
    })
    .catch((e) => {
      if (e.response && e.response.data) {
        reject(e.response.data);
      } else {
        reject(e);
      }
    })
    .finally(() => {
      loading(false);
    });
});

/**
 * update business
 * @returns {Array<business>}
 */
export const updatebusiness = (businessId: string, payload: {
  legalName?: string
  entityType?: string
  dba?: string
  email?: string
  idType?: string
  idNumber?: string,
  address?: {
    line1: string,
    line2: string,
    city: string,
    postalCode: string,
    country: string,
    addressType: string,
    state: string,
  }
}): AsyncAppAction<business> => (dispatch) => new Promise((resolve, reject) => {
  loading(true);
  http
    .patch<business>(`v1/business/${businessId}`, payload)
    .then(({ data }) => {
      dispatch(updateSelectedBusiness(data));
      resolve(data);
    })
    .catch((e) => {
      if (e.response && e.response.data) {
        reject(e.response.data);
      } else {
        reject(e);
      }
    })
    .finally(() => {
      loading(false);
    });
});

/**
 * get business by od
 * @returns {Array<business>}
 */
export const getbusinessById = (id: string, showLoader = true): AsyncAppAction<business> => () => new Promise((resolve, reject) => {
  if (showLoader) { loading(true); }
  const url = `v1/business/${id}`;

  http
    .get<business>(url)
    .then(({ data }) => {
      resolve(data);
    })
    .catch((e) => {
      if (e.response && e.response.data) {
        reject(e.response.data);
      } else {
        reject(e);
      }
    })
    .finally(() => {
      if (showLoader) { loading(false); }
    });
});

