import { actionCreator } from '..';
import { business, businessOwner } from '../../types/business';
import {
  UPDATE_BUSINESS_LIST, UPDATE_OWNERS_LIST, UPDATE_SELECTED_BUSINESS, UPDATE_SELECTED_BUSINESS_OWNER
} from '../actionTypes/business';

export const updateBusinessList: actionCreator<business[]> = (payload) => ({
  type: UPDATE_BUSINESS_LIST,
  payload,
});

export const updateSelectedBusiness: actionCreator<business> = (payload) => ({
  type: UPDATE_SELECTED_BUSINESS,
  payload,
});

export const updateOwnersList: actionCreator<businessOwner[]> = (payload) => ({
  type: UPDATE_OWNERS_LIST,
  payload,
});

export const updateSelectedBusinessOwner: actionCreator<businessOwner | null> = (payload) => ({
  type: UPDATE_SELECTED_BUSINESS_OWNER,
  payload,
});
