import React, { useEffect, useMemo, useState } from 'react';
import { useTranslation } from 'react-i18next';
import {
  Box, Text, HStack,
} from '@chakra-ui/react';
import { shallowEqual } from 'react-redux';
import Icon from '../../../../components/Icon';
import style from './BusinessDetails.module.css';
import { useAppAsyncDispatch, useAppSelector } from '../../../../hooks/store';
import MiniBox from '../../../../layout/MiniBox';
import SkeletonLoader from '../../../../components/SkeletonLoader';
import { getbusinessById } from '../../../../store/middlewares/business';
import { listAllAccounts } from '../../../../store/middlewares/account';
import { business } from '../../../../types/business';
import {
  apiErrorHandler, getDisclosures, getHelp, getInTouch
} from '../../../../utils/general';
import Map from '../../../../components/Map';
import { useDrawer } from '../../../../hooks/drawer';

const BusinessDetails = () => {
  const { t } = useTranslation();
  const dispatch = useAppAsyncDispatch();
  const [details, updateBusiness] = useState<business | null>(null);
  const accounts = useAppSelector((store) => store.account.accounts, shallowEqual);
  const { data } = useDrawer<{id: string}>();
  const id = data?.id;

  const addressString = useMemo(() => {
    if (details) {
      const { address } = details;
      return address ? `${details.address.line1}, ${details.address.line2}, ${details.address.city}, ${details.address.state}, ${address.country}` : '';
    }
    return '';
  }, [details]);

  useEffect(() => {
    if (id) {
      dispatch(getbusinessById(id)).then((b) => {
        updateBusiness(b);
      }).catch(apiErrorHandler);
      dispatch(listAllAccounts(id)).catch(apiErrorHandler);
    }
  }, [id]);

  return (
    <div className={`${style.pageBg} page`}>
      {/* (Update loader) */}
      {!details ? (
        <SkeletonLoader name="AccountDetails" />
      ) : (
        <MiniBox
          heading={t('business_profile.title')}
        >
          <Box width="100%" paddingTop="15px">
            <Text variant="large">{t('business_profile.profile')}</Text>
            <Box className={style.otherDetails}>
              <Box className={style.otherDetailsInfo}>
                <Text variant="regular" textAlign="left" className={`${style.otherDetailsInfoLabel} text-light`}>{t('business_profile.name')}</Text>
                <Text variant="regular" textAlign="left" className={style.otherDetailsInfoValue}>{details.legalName}</Text>
              </Box>
              <Box className={style.otherDetailsInfo}>
                <Text variant="regular" textAlign="left" className={`${style.otherDetailsInfoLabel} text-light`}>{t('business_profile.dba')}</Text>
                <Text variant="regular" textAlign="left" className={style.otherDetailsInfoValue}>{details.dba}</Text>
              </Box>
            </Box>

            <Text variant="large">{t('business_profile.accounts')}</Text>
            <Box className={style.otherDetails}>
              {accounts.map((a, i) => (
                <Box key={`account-${a.id}`} borderBottom={i === accounts.length - 1 ? 'none' : '1px solid var(--border-color-1)'}>
                  <Box className={style.otherDetailsInfo}>
                    <Text variant="regular" textAlign="left" className={style.otherDetailsInfoValue}>{a.label}</Text>
                    <Text variant="regular" textAlign="left" className={`${style.otherDetailsInfoLabel} text-light`}>{a.accountNumber}</Text>
                  </Box>
                </Box>
              ))}
            </Box>

            <Text variant="large" mb="16px">{t('business_profile.actions')}</Text>
            <Box marginBottom="32px">
              <Box
                role="button"
                onClick={() => {
                  getHelp();
                }}
              >
                <HStack className={style.statement}>
                  <Text variant="regular" textAlign="left">{t('business_profile.help')}</Text>
                  <Icon icon="Arrow-small-right" size={12} className="svg-light-grey-1" />
                </HStack>
              </Box>
              <Box
                role="button"
                onClick={() => {
                  getInTouch();
                }}
              >
                <HStack className={style.statement}>
                  <Text variant="regular" textAlign="left">{t('business_profile.getInTouch')}</Text>
                  <Icon icon="Arrow-small-right" size={12} className="svg-light-grey-1" />
                </HStack>
              </Box>
              <Box
                role="button"
                onClick={() => {
                  getDisclosures();
                }}
              >
                <HStack className={style.statement}>
                  <Text variant="regular" textAlign="left">{t('business_profile.disclosures')}</Text>
                  <Icon icon="Arrow-small-right" size={12} className="svg-light-grey-1" />
                </HStack>
              </Box>
            </Box>

            <Text variant="large" mb="16px">{t('business_profile.location')}</Text>
            <Box className={style.otherDetails}>
              <Box className={style.location__map}>
                <Map zoom={7} apiKey={process.env.REACT_APP_GMAP_KEY || ''} address={addressString} />

              </Box>
              <Box className={style.otherDetailsInfo1} borderBottom="1px solid var(--border-color-1)">
                <Text variant="regular" textAlign="left" className="text-light">{t('business_profile.address')}</Text>
                <Text variant="regular" textAlign="left" className={style.otherDetailsInfoValue1}>{`${details.address.line1} ${details.address.line2} ${details.address.city} ${details.address.state}`}</Text>
              </Box>
              <Box className={style.otherDetailsInfo1}>
                <Text variant="regular" textAlign="left" className="text-light">{t('business_profile.country')}</Text>
                <Text variant="regular" textAlign="left" className={style.otherDetailsInfoValue1}>{details.address.country}</Text>
              </Box>
            </Box>
          </Box>
        </MiniBox>
      )}

    </div>

  );
};

export default BusinessDetails;
